#clients {
  margin-top: 10px;
  margin-bottom: 50px;
}
.client-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 80%;
  margin: auto;
  /* border: 1px solid red; */
}
.client-container div {
  padding-top: 10px;
  width: 10%;
}
.client-container div img {
  width: 80%;
  height: auto;
  /* border: 1px solid black; */
}

#clients > h1 {
  font-size: 5vw;
  padding-top: 2vw;
  padding-bottom: 2vw;
  text-align: center;
  font-weight: bold;
}
